
.github-corner {
  position: initial !important;
  z-index: 999;
}
.github-corner:hover .octo-arm {
    -webkit-animation: octocat-wave 560ms ease-in-out;
            animation: octocat-wave 560ms ease-in-out;
}
@media (max-width: 500px) {
.github-corner:hover:hover .octo-arm {
      -webkit-animation: none;
              animation: none;
}
.github-corner:hover .octo-arm {
      -webkit-animation: octocat-wave 560ms ease-in-out;
              animation: octocat-wave 560ms ease-in-out;
}
}
@-webkit-keyframes octocat-wave {
0%,
  100% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
20%,
  60% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
}
40%,
  80% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
}
}
@keyframes octocat-wave {
0%,
  100% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
20%,
  60% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
}
40%,
  80% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
}
}
body {
  height: 90vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-size: 200%;
  background-image: -webkit-gradient(linear, right top, left bottom, from(#fe87c3), color-stop(50%, #828dfe), to(#6d58f3));
  background-image: linear-gradient(to bottom left, #fe87c3 0%, #828dfe 50%, #6d58f3 100%);
  -webkit-transition: 600ms ease;
  transition: 600ms ease;
}
body:hover {
  background-position: 100% 0;
}



